import memoizeOne from 'memoize-one';
import { writable } from 'svelte/store';
import { formatter } from './log-formatter';
const LOG_WINDOW_SIZE = 100;
const LATEST_LOG_WINDOW_SIZE = 2;
function createLogStore(initialValue) {
    // use default values, if needed
    if (!initialValue) {
        initialValue = {
            mode: 'tail',
            count: 0,
            filter: '',
            formatter: formatter.getFormatter(),
            columns: [],
            window: [],
            latest: [],
        };
    }
    const { subscribe, update } = writable(initialValue);
    // @ts-ignore JSL_ENVIRONMENT is being replaced by @rollup/plugin-replace
    const serverAddress = JSL_ENVIRONMENT == 'production' ? window.location.host : 'localhost:8000';
    const ws = new WebSocket(`ws://${serverAddress}/`);
    ws.onopen = function () {
        console.log('WebSocket Client Connected');
    };
    ws.onmessage = function (e) {
        const msg = decode(e.data);
        update(currentValue => {
            const columns = Object.keys(parseFormatter(currentValue.formatter));
            const logFormatter = formatLogMessage(currentValue.formatter);
            if (msg.type === 'init') {
                if (msg.mode === 'tail') {
                    return {
                        mode: msg.mode,
                        count: msg.size,
                        filter: currentValue.filter,
                        formatter: currentValue.formatter,
                        columns,
                        window: msg.window.map(logFormatter),
                        latest: [],
                    };
                }
                else if (msg.mode === 'static') {
                    return {
                        mode: msg.mode,
                        count: msg.size,
                        filter: currentValue.filter,
                        formatter: currentValue.formatter,
                        columns,
                        window: msg.window.map(logFormatter),
                        offsetSeq: msg.offsetSeq,
                        latest: [],
                    };
                }
            }
            else if (msg.type === 'update') {
                if (currentValue.mode === 'tail') {
                    return Object.assign(Object.assign({}, currentValue), { count: msg.size, window: [...currentValue.window.slice(-LOG_WINDOW_SIZE), logFormatter(msg.message)] });
                }
                else if (currentValue.mode === 'static') {
                    return Object.assign(Object.assign({}, currentValue), { count: msg.size, latest: [
                            ...currentValue.latest.slice(-LATEST_LOG_WINDOW_SIZE),
                            logFormatter(msg.message),
                        ] });
                }
            }
        });
    };
    const sendToServer = (msg) => ws.send(encode(msg));
    return {
        subscribe,
        changeFilter: (newFilter) => {
            update(state => {
                if (state.mode == 'tail') {
                    sendToServer({ mode: state.mode, filter: newFilter });
                }
                else if (state.mode == 'static') {
                    sendToServer({ mode: state.mode, offsetSeq: state.offsetSeq, filter: newFilter });
                }
                return Object.assign(Object.assign({}, state), { filter: newFilter });
            });
        },
        changeToTail: () => {
            update(state => {
                sendToServer({ mode: 'tail', filter: state.filter });
                return Object.assign(Object.assign({}, state), { mode: 'tail', offsetSeq: undefined });
            });
        },
        changeToStatic: (offsetSeq) => {
            update(state => {
                sendToServer({ mode: 'static', offsetSeq, filter: state.filter });
                return Object.assign(Object.assign({}, state), { mode: 'static', offsetSeq });
            });
        },
        changeFormatter: (newFormatter) => {
            update(state => {
                // re-fetch data from the server so it can be formatted with the new formatter
                sendToServer({
                    mode: state.mode,
                    offsetSeq: state.mode == 'static' ? state.offsetSeq : undefined,
                    filter: state.filter,
                });
                // save the new formatter
                formatter.updateFormatter(newFormatter);
                return Object.assign(Object.assign({}, state), { formatter: newFormatter });
            });
        },
    };
}
const encode = (data) => JSON.stringify(data);
const decode = (data) => JSON.parse(data);
const formatLogMessage = (formatterStr) => {
    const formatter = parseFormatter(formatterStr);
    return (msg) => ({
        seq: msg.seq,
        rawMessage: msg.data,
        formattedMessage: Object.keys(formatter).reduce((acc, key) => (Object.assign(Object.assign({}, acc), { [key]: execFn(formatter[key], msg) })), {}),
    });
};
const execFn = (fn, msg) => {
    try {
        const res = fn(msg.data, msg.seq);
        return res == undefined ? '' : res;
    }
    catch (err) {
        return '';
    }
};
const parseFormatter = memoizeOne((formatter) => {
    let result;
    try {
        result = eval(formatter);
    }
    catch (err) {
        throw err;
        // console.error('Invalid formatter object. Reverting to default format')
        // console.log(err)
        // result = eval(DEFAULT_LOG_FORMATTER)
    }
    return result;
});
const logStore = createLogStore();
export { logStore, formatLogMessage, parseFormatter };
